import {Injectable} from '@angular/core';
import {Router} from '@angular/router';
import {ApiService} from '@core/services';
import {HanldeReportService} from '@modules/handle-report/hanlde-report.service';

@Injectable({
  providedIn: 'root'
})
export class UserManualService {

  constructor(
    private router: Router,
    private apiService: ApiService,
  ) {
  }

  getAllNotify() {
    return this.apiService.get('/notifies?sort[0][key]=created_at&sort[0][direction]=DESC');
  }

  getNotifyById(idNotify, value: boolean) {
    if (value) {
      return this.apiService.get(`/notifies/${idNotify}?options[update_read]=1`);
      } else {
      return this.apiService.get(`/notifies/${idNotify}`);
    }
  }
}
