import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {AuthService} from '@core/services';
import {TranslateService} from '@ngx-translate/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Library} from '@core/library';
import {map} from 'rxjs/operators';
import * as moment from 'moment';
import {UserManualService} from '@modules/user-manual/user-manual.service';
import data from 'devextreme/bundles/dx.all';

@Component({
  selector: 'app-notify',
  templateUrl: './notify.component.html',
  styleUrls: ['./notify.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class NotifyComponent implements OnInit {
  _translate: any;
  _language: string;
  dataSource: Array<any> = [];
  textTitle: string;
  timeNotify: string;
  selectedRows: Array<any> = [];
  textDate: any;
  testCss = true;
  contentHtml: string;

  constructor(
    private authService: AuthService,
    public translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private userManualService: UserManualService
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.translate.use(this._language);
  }

  ngOnInit() {
    this.route.data.pipe(map(value => value.NotifyResolverService)).subscribe(
      value => {
        this.dataSource = value.notifies;
        this.selectedRows.push(this.dataSource[0]);
        if (value.notifies.length > 0) {
          this.getContentdefault(value.notifies, false);
        }
      },
      error => {
        Library.notify(error, 'error');
      },
    );
  }

  getContentdefault(value, isRead) {
    Library.showloading();
    this.userManualService.getNotifyById(value[0].id, isRead).subscribe(
      data1 => {
        if (data1.status === 'success') {
          this.renderData(data1.data.notify, isRead);
        }
      }, error => {
        Library.notify(error, 'error');
      },
      () => Library.hideloading()
    );
  }

  routerNavigate() {
    this.router.navigate(['/system/user-manual/manual']);
  }

  selectionChanged($event: any) {
    if (this.selectedRows.length > 0) {
      this.getContentdefault(this.selectedRows, true);
    }
  }

  formatTime(created_at: any) {
    return moment(created_at).format('DD/MM/YYYY HH:mm');
  }

  renderData(value, isRead) {
    this.textTitle = value.title;
    this.contentHtml = value.content;
    this.textDate = this.formatTime(value.created_at);
    if (isRead) {
      for (let i = 0; i < this.dataSource.length; i++) {
        if (value.id === this.dataSource[i].id) {
          this.dataSource[i].read_status = true;
        }
      }
    }
  }
}
