import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {NotifyComponent} from '@modules/user-manual/components/notify/notify.component';
import {ManualComponent} from '@modules/user-manual/components/manual/manual.component';
import {NotifyResolverService} from '@modules/user-manual/shared/notify-resolver.service';

const routes: Routes = [
  {path: 'manual', component: ManualComponent, data: {brec: 'Hướng dẫn sử dụng'}},
  // {path: 'notify', component: NotifyComponent, data: {brec: 'Thông báo sự kiện'}, resolve: {NotifyResolverService}},
  {path: '**', redirectTo: '/'},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ManualRouting {
}
